import { cn } from "@/lib/utils";
import { motion } from "framer-motion";

interface CommonBannerProps {
  title: string;
  subtitle?: React.ReactNode; // Changed to ReactNode to accept custom components
  buttonText?: string;
  onButtonClick?: () => void;
  className?: string;
}

const CommonBanner = ({
  title,
  subtitle,
  buttonText,
  onButtonClick,
  className,
}: CommonBannerProps) => {
  return (
    <div
      className={cn(
        "relative w-full bg-primary overflow-hidden py-12 sm:py-16 lg:py-20",
        className
      )}
    >
      <div className="container mx-auto px-4 relative z-10">
        <div
          className={cn(
            "mx-auto",
            buttonText
              ? "flex flex-col sm:flex-row items-center justify-between gap-6"
              : "text-center max-w-3xl"
          )}
        >
          <div className={cn("space-y-4", buttonText ? "flex-1" : "mx-auto")}>
            <motion.h1
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.5 }}
              className="text-2xl sm:text-3xl md:text-4xl font-bold text-white"
            >
              {title}
            </motion.h1>

            {subtitle && (
              <motion.div
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ duration: 0.5, delay: 0.1 }}
                className="text-sm sm:text-base md:text-lg text-white/90"
              >
                {subtitle}
              </motion.div>
            )}
          </div>

          {buttonText && (
            <motion.div
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.5, delay: 0.2 }}
              className="shrink-0"
            >
              <button
                onClick={onButtonClick}
                className={cn(
                  "px-6 py-3 rounded-lg",
                  "bg-white text-primary font-medium",
                  "transform transition-all duration-200",
                  "hover:bg-white/90 hover:scale-105",
                  "text-sm sm:text-base",
                  "whitespace-nowrap"
                )}
              >
                {buttonText}
              </button>
            </motion.div>
          )}
        </div>
      </div>
    </div>
  );
};

export default CommonBanner;
